import React from 'react'
import { Link } from 'react-router-dom'

function Header() {
  return (
    <>
    <header>
        <span>SEO-PORTAL</span>
        <nav>
            <Link to={'/'}> Home</Link>
            <Link to={'/'}> Service</Link>
            <Link to={'/'}> Blogs</Link>
            <Link to={'/'}> Contact</Link>
        </nav>
    </header>
    
    
    </>
  )
}

export default Header